import { useMemo } from "react";
import { Currency, OrUndefined, SortingOrder } from "../../@types/types";

const useSortCurrencies = (
  currencies: OrUndefined<Currency[]>,
  sortingOrder: SortingOrder
) => {
  const sortedCurrencies = useMemo(() => {
    const sortedCurrencies = currencies ? [...currencies] : [];

    switch (sortingOrder) {
      case "byCurrencyNameAscending":
        sortedCurrencies.sort((a, b) => a.name.localeCompare(b.name));
        break;
      case "byCurrencyNameDescending":
        sortedCurrencies.sort((a, b) => b.name.localeCompare(a.name));
        break;
      case "byAverageExchangeRateAscending":
        sortedCurrencies.sort(
          (a, b) => a.averageExchangeRate - b.averageExchangeRate
        );
        break;
      case "byAverageExchangeRateDescending":
        sortedCurrencies.sort(
          (a, b) => b.averageExchangeRate - a.averageExchangeRate
        );
        break;
      default:
        break;
    }

    return sortedCurrencies;
  }, [sortingOrder, currencies]);

  return sortedCurrencies;
};

export default useSortCurrencies;
