import { useEffect, useState } from "react";
import {
  ActionStatus,
  CurrenciesExchangeRateFromApi,
  OrNull,
} from "../../@types/types";
import { fetchCurrenciesExchangeRate } from "../../api/api";

const useFetchCurrenciesExchangeRateFromApi = (
  setFetchingOperationStatus: React.Dispatch<React.SetStateAction<ActionStatus>>
) => {
  const [currenciesExchangeRateFromApi, setCurrenciesExchangeRateFromApi] =
    useState<OrNull<CurrenciesExchangeRateFromApi>>(null);

  useEffect(() => {
    setFetchingOperationStatus("PROCESSING");

    const fetchData = async () => {
      try {
        const receivedData = await fetchCurrenciesExchangeRate();
        setCurrenciesExchangeRateFromApi(receivedData);

        setFetchingOperationStatus("SUCCEEDED");
      } catch (error) {
        console.error(error);

        setFetchingOperationStatus("FAILED");
      }
    };

    fetchData();
  }, [setFetchingOperationStatus]);

  return currenciesExchangeRateFromApi;
};

export default useFetchCurrenciesExchangeRateFromApi;
