import {
  Container,
  Paper,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Typography,
} from "@mui/material";
import { useCallback, useState } from "react";
import { ActionStatus, OrNull, SortingOrder } from "./@types/types";
import "./App.css";
import useSortCurrencies from "./components/hooks/useSortCurrencies";
import useFetchCurrenciesExchangeRateFromApi from "./components/hooks/useFetchCurrenciesExchangeRateFromApi";
import { ErrorIndicator } from "./components/ErrorIndicator";
import { LoadingIndicator } from "./components/LoadingIndicator";
import SortableTableHeader from "./components/SortableTableHeader";
import TableRowWithCurrencyExchangeRate from "./components/TableRow";

function App() {
  const [sortingOrder, setSortingOrder] = useState<OrNull<SortingOrder>>(null);

  const [fetchingDataOperationStatus, setFetchingDataOperationStatus] =
    useState<ActionStatus>("PROCESSING");

  const currenciesExchangeRateFromApi = useFetchCurrenciesExchangeRateFromApi(
    setFetchingDataOperationStatus
  );

  const sortedCurrencies = useSortCurrencies(
    currenciesExchangeRateFromApi?.currencies,
    sortingOrder
  );

  const setSortingOrderByCurrencyName = useCallback(() => {
    setSortingOrder((prev) =>
      prev === "byCurrencyNameDescending"
        ? "byCurrencyNameAscending"
        : "byCurrencyNameDescending"
    );
  }, []);

  const setSortingOrderByAverageExchangeRate = useCallback(() => {
    setSortingOrder((prev) =>
      prev === "byAverageExchangeRateDescending"
        ? "byAverageExchangeRateAscending"
        : "byAverageExchangeRateDescending"
    );
  }, []);

  if (fetchingDataOperationStatus === "FAILED") {
    return <ErrorIndicator />;
  } else if (fetchingDataOperationStatus === "PROCESSING") {
    return <LoadingIndicator />;
  } else {
    return (
      <Container
        maxWidth="md"
        component={Paper}
        elevation={6}
        sx={{
          marginTop: "3rem",
        }}
      >
        <Typography
          variant="h4"
          component="h4"
          sx={{ textAlign: "center", pt: " 1rem", pb: "1rem" }}
        >
          Tabela nr {currenciesExchangeRateFromApi?.tableNr} z dnia{" "}
          {currenciesExchangeRateFromApi?.publicationDate}
        </Typography>
        <TableContainer
          sx={{
            "& .MuiTableCell-head": {
              fontSize: "1.5rem",
            },
          }}
        >
          <Table size="small" aria-label="kursy wymiany walut">
            <TableHead>
              <TableRow>
                <TableCell align="center" sx={{ width: "40%" }}>
                  <SortableTableHeader
                    sortingOrder={
                      sortingOrder === "byCurrencyNameAscending"
                        ? "ascending"
                        : sortingOrder === "byCurrencyNameDescending"
                        ? "descending"
                        : null
                    }
                    sortingFunction={setSortingOrderByCurrencyName}
                    headerText="Nazwa waluty"
                  />
                </TableCell>
                <TableCell align="right" sx={{ width: "30%" }}>
                  <Typography sx={{ fontSize: "inherit" }}>
                    Kod waluty
                  </Typography>
                </TableCell>
                <TableCell sx={{ width: "30%" }}>
                  <SortableTableHeader
                    sortingOrder={
                      sortingOrder === "byAverageExchangeRateAscending"
                        ? "ascending"
                        : sortingOrder === "byAverageExchangeRateDescending"
                        ? "descending"
                        : null
                    }
                    sortingFunction={setSortingOrderByAverageExchangeRate}
                    headerText="Kurs średni(g)"
                  />
                </TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {sortedCurrencies.map((currency) => (
                <TableRowWithCurrencyExchangeRate
                  key={currency.code}
                  currency={currency}
                />
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      </Container>
    );
  }
}

export default App;
