import { Stack, Typography } from "@mui/material";
import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import ArrowDropUpIcon from "@mui/icons-material/ArrowDropUp";
import React from "react";

const SortableTableHeader = ({
  sortingOrder,
  sortingFunction,
  headerText,
}: {
  sortingOrder: "ascending" | "descending" | null;
  sortingFunction: () => void;
  headerText: string;
}) => {
  return (
    <Stack
      direction="row"
      alignItems="center"
      onClick={sortingFunction}
      sx={{ cursor: "pointer" }}
    >
      {sortingOrder === "ascending" ? (
        <ArrowDropUpIcon sx={{ fontSize: "1.8rem" }} />
      ) : sortingOrder === "descending" ? (
        <ArrowDropDownIcon sx={{ fontSize: "1.8rem" }} />
      ) : null}{" "}
      <Typography sx={{ fontSize: "inherit" }}>{headerText}</Typography>
    </Stack>
  );
};

export default React.memo(SortableTableHeader);
